import React, { FC, useEffect, useRef } from 'react';
import logo from '../assets/logo.svg';
import '../styles/NavBar.scss';
import { NavLink } from 'react-router-dom';

export type TNav = { title: string; link: string };
const navBar: TNav[] = [
	{ title: 'Home', link: '/' },
	{ title: 'Programs', link: '/programs' },
	{ title: 'Registration', link: '/registration' },
	{ title: 'Achievements', link: '/achievements' },
	{ title: 'Gallery', link: '/gallery' },
	{ title: 'About us', link: '/about' }
];
const NavBar: FC = () => {
	const nav = useRef<HTMLElement>(null);

	useEffect(() => {
		window.onscroll = () => {
			if (nav.current) {
				if (window.pageYOffset === 0) {
					nav.current.className = 'nav-container';
				} else {
					nav.current.className = 'nav-container nav-scroll';
				}
			}
		};
	}, []);
	return (
		<nav className='nav-container' ref={nav}>
			<div className='nav-symbol'>
				<img className='nav-logo' src={logo} alt='WebLogo' />
				<div className='nav-title'>Morningstar Educational Group Inc.</div>
			</div>
			<div className='nav-bar'>
				{navBar.map((item) => (
					<NavLink
						to={item.link}
						exact
						className='nav-link'
						activeClassName='nav-link-active'>
						{item.title}
					</NavLink>
				))}
			</div>
			<div className='nav-bar-underline'></div>
		</nav>
	);
};

export default NavBar;
