import { IAppDataState } from './Interface';
import React, { createContext, useContext } from 'react';
import { appDataReducer, IAction } from '../components/reducer/AppReducer';
import { useImmerReducer } from 'use-immer';

const reqImages = require.context('../assets/homePageImages', true, /\.jpg$/);
const images = reqImages.keys().map((path) => reqImages(path));

const initialState: IAppDataState = {
	imgURL: images[0]
};

const stateCtx = createContext(initialState);
const dispatchCtx = createContext((() => null) as React.Dispatch<IAction>);

const AppCtxProvider: React.ComponentType = (props) => {
	const [state, dispatch] = useImmerReducer(appDataReducer, initialState);
	return (
		<dispatchCtx.Provider value={dispatch}>
			<stateCtx.Provider value={state}>{props.children}</stateCtx.Provider>
		</dispatchCtx.Provider>
	);
};

export default AppCtxProvider;
export const useDispatch = () => useContext(dispatchCtx);
export const useAppData = <K extends keyof IAppDataState>(property: K) => {
	const state = useContext(stateCtx);
	return state[property];
};
